
const baseSuccessCallback = (event) => {
  event.preventDefault();
  // –í –¥–∞–Ω–Ω–æ–º –∫–æ–ª–±–µ–∫–µ –±—ç–∫–µ–Ω–¥–µ—Ä, –ª–∏–±–æ —Ä–∞–∑—Ä–∞–±–æ—Ç—á–∏–∫ –ø—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏ –±—É–¥–µ—Ç –ø–∏—Å–∞—Ç—å –∑–∞–ø—Ä–æ—Å –Ω–∞ –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä –∏ –æ–±—Ä–∞–±–∞—Ç—ã–≤–∞—Ç—å –≤–æ–∑–º–æ–∂–Ω—ã–µ –æ—à–∏–±–∫–∏ –∏–ª–∏ —É—Å–ø–µ—à–Ω—É—é –æ—Ç–ø—Ä–∞–≤–∫—É —Ñ–æ—Ä–º—ã –Ω–∞ —Å–µ—Ä–≤–µ—Ä
};

const baseErrorCallback = (event) => {
  event.preventDefault();
  // –î–∞–Ω–Ω—ã–π –∫–æ–ª–ª–±–µ–∫ –∏—Å–ø–æ–ª—å–∑—É–µ—Ç—Å—è –ø—Ä–∏ –Ω–µ–æ–±—Ö–æ–¥–∏–º–æ—Å—Ç–∏ –≤—ã–ø–æ–ª–Ω–∏—Ç—å –∫–∞–∫–æ–µ-–ª–∏–±–æ –¥–µ–π—Å—Ç–≤–∏–µ –ø–æ–º–∏–º–æ –ø–æ–∫–∞–∑–∞ –æ—à–∏–±–æ–∫ –ø—Ä–∏ –ø–æ–ø—ã—Ç–∫–µ –æ—Ç–ø—Ä–∞–≤–∏—Ç—å –Ω–µ–∫–∫–æ—Ä—Ä–µ–∫—Ç–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ, –æ–Ω –Ω–µ —Å–≤—è–∑–∞–Ω —Å –∑–∞–ø—Ä–æ—Å–∞–º–∏ –Ω–∞ —Å–µ—Ä–≤–µ—Ä
};

export const callbacks = {
  base: {
    // –°–±—Ä–æ—Å—Å —Ñ–æ—Ä–º—ã
    reset: true,
    // –¢–∞–π–º–∞—É—Ç —Å–±—Ä–æ—Å–∞ —Ñ–æ—Ä–º—ã
    resetTimeout: 500,
    successCallback: baseSuccessCallback,
    errorCallback: baseErrorCallback,
  },
};
