const setLimitationError = (limitation) => {
  // eslint-disable-next-line no-console
  console.error(`–ü–µ—Ä–µ–¥–∞–Ω–Ω—ã–π —Ñ–æ—Ä–º–∞—Ç –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏—è(data-limitation="${limitation}") - –Ω–µ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç—Å—è. –ü—Ä–æ–≤–µ—Ä—å—Ç–µ –∫–æ—Ä—Ä–µ–∫—Ç–Ω–æ—Å—Ç—å –≤–≤–µ–¥—ë–Ω–Ω—ã—Ö –∑–Ω–∞—á–µ–Ω–∏–π.`);
};

export const getLimitationsRegEx = (limitation) => {
  switch (limitation) {
    case 'digit':
      return /[^\d]/g;
    case 'name':
      return /[^a-zA-Z–∞-—è—ë–ê-–Ø–Å\-\s]/g;
    case 'letters':
      return /[^a-zA-Z–∞-—è—ë–ê-–Ø–Å\s]/g;
    case 'letters-and-digit':
      return /[^a-zA-Z–∞-—è—ë–ê-–Ø–Å\s\d]/g;
    case 'cyrillic':
      return /[^–∞-—è—ë–ê-–Ø–Å\s]/g;
    case 'latin':
      return /[^a-zA-Z\s]/g;
    default:
      return setLimitationError(limitation);
  }
};

export const getMatrixLimitationsRegEx = (matrix) => {
  switch (matrix) {
    case 'digit':
      return /[^\d]/g;
    case 'name':
      return /[^\–∞-—è—ë\–ê-–Ø–Å\a-z\A-Z\-]]/g;
    case 'letters':
      return /[^\–∞-—è—ë\–ê-–Ø–Å\a-z\A-Z]/g;
    case 'letters-and-digit':
      return /[^\–∞-—è—ë\–ê-–Ø–Å\a-z\A-Z\d]/g;
    case 'cyrillic':
      return /[^\–∞-—è—ë\–ê-–Ø–Å]/g;
    case 'latin':
      return /[^\a-z\A-Z]/g;
    default:
      return false;
  }
};

export const getMailRegEx = () => /[a-zA-Z–∞-—è—ë–ê-–Ø–Å0-9]{1}([a-zA-Z–∞-—è—ë–ê-–Ø–Å0-9\-_\.]{1,})?@[a-zA-Z–∞-—è—ë–ê-–Ø–Å0-9\-]{1}([a-zA-Z–∞-—è—ë–ê-–Ø–Å0-9.\-]{1,})?[a-zA-Z–∞-—è—ë–ê-–Ø–Å0-9\-]{1}\.[a-zA-Z–∞-—è—ë–ê-–Ø–Å]{2,6}/;
