export const matrixReplace = (item, matrix, RegEx) => {
  if (!matrix) {
    // eslint-disable-next-line no-console
    console.error('–ü—Ä–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –ø–æ –º–∞—Ç—Ä–∏—Ü–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ —É–∫–∞–∑—ã–≤–∞—Ç—å —Ñ–æ—Ä–º–∞—Ç –º–∞—Ç—Ä–∏—Ü—ã: data-matrix=""');
    item.value = '';
    return;
  }

  if (!RegEx) {
    // eslint-disable-next-line no-console
    console.error('–ü—Ä–∏ –≤–∞–ª–∏–¥–∞—Ü–∏–∏ –ø–æ –º–∞—Ç—Ä–∏—Ü–µ –æ–±—è–∑–∞—Ç–µ–ª—å–Ω–æ —É–∫–∞–∑—ã–≤–∞—Ç—å —Ñ–æ—Ä–º–∞—Ç –æ–≥—Ä–∞–Ω–∏—á–µ–Ω–∏–π: data-matrix-limitations=""');
    item.value = '';
    return;
  }

  const def = matrix.replace(RegEx, '');
  let val = item.value.replace(RegEx, '');
  let i = 0;

  if (def.length >= val.length) {
    val = def;
  }

  item.value = matrix.replace(/./g, (a) => {
    if (/[_\^]/.test(a) && i < val.length) {
      return val.charAt(i++);
    } else if (i >= val.length) {
      return '';
    } else {
      return a;
    }
  });
};
